
















































































/*  addlist*/
.add-enter-active[data-v-5e4fad0b], .add-leave-active[data-v-5e4fad0b]{
    -webkit-transition: all .3s;
    transition: all .3s;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
}
.add-enter[data-v-5e4fad0b], .add-leave-to[data-v-5e4fad0b]{
    -webkit-transition: all .3s;
    transition: all .3s;
    -webkit-transform: translate3d(100%,0,0);
            transform: translate3d(100%,0,0);
    opacity: 0;
}
.addListBody[data-v-5e4fad0b]{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 20;
    background: #222;
}
.add-list-header[data-v-5e4fad0b]{
    position: relative;
    z-index: 20;
    height: 88px;
    width: 100%;
    line-height: 50px;
    font-size: 16px;
    color: #fff;
    background: #222;
    text-align: center;
    margin-bottom: 20px;
    /*overflow: hidden;*/
}
.add-title[data-v-5e4fad0b]{
    /*float: right;*/
    width: 90%;
    height: 50px;
}
.icon-close[data-v-5e4fad0b]{
    position: absolute;
    right: 10px;
    /*float: right;*/
   /* margin-left: 40%;*/
    width: 10%;
    height: 50px;
    font-size: 18px;
    color: solid hsla(0,0%,100%,.3);
}
.search-box-recently[data-v-5e4fad0b] {
    width: 90%;
    height: 32px;
    line-height: 32px;
    text-align: center;
    color: #999;
    font-size: 14px;
    border-radius: 5px;
    margin: 0 auto;
    border: 1px solid hsla(0,0%,100%,.1);
}
.box-recently-item[data-v-5e4fad0b] {
    float: left;
    text-align: center;
    width: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.box-recently-item[data-v-5e4fad0b]:first-child{
    border-right: 1px solid hsla(0,0%,100%,.1);
}
.search-box-recently .recently-active[data-v-5e4fad0b] {
    background: #333;
    color: #fff;
}
.add-histroy[data-v-5e4fad0b]{
    position: absolute;
    top: 98px;
    left: 0;
    right: 0;
    bottom: 0;
}
.add-histroy-ul[data-v-5e4fad0b]{
    width: 100%;
}
.add-histroy-li[data-v-5e4fad0b]{
    width: 100%;
    height: 70px;
    /*line-height: 50px;*/
    text-align: left;
    text-indent: 20px;
    font-size: 14px;
}
.histroy-li-item-n[data-v-5e4fad0b]{
    height: 40px;
    line-height: 40px;
    font-size: 15px;
    color: #fff;
}
.histroy-li-item-s[data-v-5e4fad0b]{
    height: 30px;
    line-height: 10px;
    font-size: 12px;
    color: hsla(0,0%,100%,.3);
}
.top-tips-text[data-v-5e4fad0b]{
    display: inline-block;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 14px;
    color: hsla(0,0%,100%,.5);
}



