
.search-history-box{
  margin-top: 10px;
  width: 100%;
  position: absolute;
  bottom: 0;
  top: 200px;
  left: 0;
  right: 0;
}
.search-history-list{
  position: absolute;
  overflow: hidden;
  height: 100%;
  top:30px;
  bottom: 0;
  left: 0;
  right: 0;
}
.search-history-title{
  width: 100%;
  height: 30px;
  line-height: 30px;
  color: #999;
  font-size: 14px;
  font-weight: 600;
  text-indent: 5px;
  overflow: hidden;
}
.search-history-tips{
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 13px;
  color: hsla(0,0%,100%,.2);
}
.history-icon{
  float: right;
  width: 6%;
  height: 100%;
  font-size: 16px;
  text-indent: 0;
  text-align: center;
  color: hsla(0,0%,100%,.3);
}
.searchBody{
  position: absolute;
  top: 88px;
  bottom: 0;
  width: 100%;
  min-width: 320px;
  overflow: hidden;
  background:#222;
  z-index: 10;
}
.search-box-postion{
  position: absolute;
  /*width: 90%;*/
  top: 10px;
  left: 5%;
  right: 5%;
  bottom: 0;
}
.search-hot-box{
  width: 100%;
  margin-top: 20px;
  /*  max-height: 150px;*/
  height: 150px;
  /*  min-height: 30px;*/
  background: #222;
  overflow: hidden;
}
.search-hot-title{
  width: 100%;
  height: 30px;
  line-height: 30px;
  margin-bottom: 10px;
  color: #999;
  font-size: 14px;
  font-weight: 600;
  text-indent: 5px;
  overflow: hidden;
}
.hot-key{
  float: left;
  max-width: 17%;
  margin: 5px 2%;
  padding: 0 2%;
  border-radius: 8px;
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  color: hsla(0,0%,100%,.3);
  font-weight: 400;
  background: #333;
  overflow: hidden;
}
.no-hotkey{
  width: 100%;
  height: 80px;
  font-size: 16px;
  color: hsla(0,0%,100%,.1);
  text-align: center;
  line-height: 80px;
}
.search-query-box{
  position: fixed;
  bottom: 0;
  top: 160px;
  left: 0;
  right: 0;
  z-index: 10;
  overflow: hidden;
  background: #222;
}
.refresh-box {
  width: 100%;
  height: 60px;
  overflow: hidden;
  text-align: center;
  line-height: 60px;
  font-size: 14px;
  color: hsla(0,0%,100%,.5);
}
.tips-text{
  display: inline-block;
  width: 100%;
  height: 90px;
  text-align: center;
  line-height: 100px;
  color: hsla(0,0%,100%,.5);
  font-size: 15px;
}



