html{font-size: 14px; font-family: PingFang SC,STHeitiSC-Light,Helvetica-Light,arial,sans-serif; min-width: 320px;}
body {margin:0; padding:0; user-select: none; -webkit-text-size-adjust: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); line-height: 1; background: #222; background-color: #222;}
div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,blockquote,p{padding:0; margin:0;}
ol,ul {list-style:none;}
li{list-style-type:none;}
img{vertical-align:top;border:0;max-width: 100%; height:auto;}
h1,h2,h3,h4,h5,h6{font-size:inherit; font-weight:normal;}
address,cite,code,em,th,i,var{font-weight:normal; font-style:normal;}
.clearfix{*zoom:1;}
.clearfix:after{display:block; overflow:hidden; clear:both; height:0; visibility:hidden; content:".";}
a:link,a:visited
{
	color:#000;
	text-decoration: none;
}
input
{
	outline: 0;
	outline: none;
}
.ellipsis
{
	overflow: hidden;
	text-overflow:ellipsis;
	white-space: nowrap;
}
.loading{
	background: url(../../assets/logo/loading.png);
	background-repeat: no-repeat;
	background-position:center;
}
.lazy-img{
	visibility: hidden;
}


