#footer{
  width: 100%;
  height: 60px;
  line-height: 60px;
  background: #222;
  text-align: center;
  color: hsla(0,0%,100%,.1);
  font-size: 16px;
}
#footer span {
  display: block;
  text-align: center;
}
#footer .author{
  height: 30px;
  line-height: 30px;
  color: #999;
  font-size: 12px;
  font-weight: 600;
}
#footer .email{
  height: 30px;
  line-height: 30px;
  color: #999;
  font-size: 10px;
  font-weight: 400;
}


